export default {
  name: 'SwitchContainerMini',
  props: {
    value: {
      type: Boolean,
      required: true
    },
    desc: {
      type: String,
      required: false,
      default: null
    }
  },
  data () {
    return {
      state: this.value
    }
  },
  watch: {
    value: {
      immediate: true,
      handler (val) {
        this.state = val
      }
    }
  },
  methods: {
    handleInput (e) {
      this.state = !this.state
      this.$emit('input', this.state)
    }
  }
}
